@use "../base/variables.scss" as *;
@use "../base/mixins.scss" as *;

.main-container__interest {
  margin: 0 auto;
  padding-top: 39px;
  padding-bottom: 49px;
}

.main-container__video {
  margin: 0 auto;
  padding-top: 18px;
}

.main-container__form {
  margin: 0 auto 22px;
}

@media (max-width: $mobile-width-only) {
  .main-container__map {
    margin-bottom: 8px;
  }
}

@media (min-width: $tablet-width) {
  .main-container__presentation {
    margin: 0 auto;
    margin-bottom: 58px;
  }

  .main-container__interest {
    padding-top: 60px;
    padding-bottom: 55px;
  }

  .main-container__video {
    margin: 0 auto;
    padding-top: 0;
    padding-bottom: 40px;
  }

  .main-container__form {
    margin-bottom: 64px;
  }
}

@media (min-width: $desktop-width) {
  .main-container__presentation {
    margin-bottom: 62px;
  }

  .main-container__interest {
    padding-bottom: 66px;
  }

  .main-container__video {
    padding-bottom: 85px;
  }
}
